<template>
  <el-dialog
    v-model="dialogVisible"
    :title="$t('swap.selectToken')"
    width="420px"
    custom-class="tokens"
  >
    <div class="flex-column-start tokens-content">
        <div class="flex-row-start align-center">
            <input type="text" class="tokens-input flex-1" v-model.trim="searchTxt" placeholder="Search name or paste address" />
            <el-button class="MR-20" @click="fastImport()" :disabled="!searchTxt || searchTxt.length != 42 && !importTokens.name">{{ t('swap.fastImport') }}</el-button>
        </div>
        
        <div class="flex-h__1 tokens-list">
            <div class="tokens-list__import flex-row-start align-center PL-30 PR-30 PB-30 PT-30" v-if="importTokens.name">
                <div class="flex-row-start flex-1 align-center">
                    <img v-lazy="getLogo(importTokens.address)" class="tokens-list__item-icon MR-10"/>
                    <div class="tokens-list__import-name MR-10">{{importTokens.name}}</div>
                    <div class="tokens-list__import-desc">{{importTokens.symbol}}</div>
                </div>
                <el-button class="tokens-list__import-btn" @click="importHandler">{{ t('swap.import') }}</el-button>
            </div>
            <template v-else>
                <div 
                    class="tokens-list__item pointer flex-row-start align-center pointer"
                    v-for="(item, index) in getTokens"
                    :key="item.address"
                    @click="selectHandler(item)"
                >
                    <img v-lazy="getLogo(item.address)" class="tokens-list__item-icon MR-10"/>
                    <div class="flex-1">
                        <div class="token-list__item-title">{{item.name}}</div>
                        <div class="token-list__item-desc">{{item.symbol}}</div>
                    </div>
                    <div class="token-list__item-price">{{ item.balance ? getBit(utils.formatUnits(item.balance, item.decimals || 18), 5) : '' }}</div>
                    <el-icon :size="30" color="#fff" class="ML-10" @click.stop="removeTokens(index)" v-if="item.isAdd"><Close /></el-icon>
                </div>
            </template>
            
        </div>
    </div>
  </el-dialog>
</template>

<script>
import { utils } from 'ethers'
import { getBit, Tips, getLogo } from '../../../libs/utils'
import { mapGetters } from 'vuex'
import { toRaw } from 'vue'
export default {
    name: 'Tokens',
    data() {
        return {
            utils,
            getBit,
            getLogo,
            dialogVisible: false,
            activeAddress: [],
            searchTxt: '',
            getTokens: [],
            // 需要导入的token信息
            importTokens: {},
            tokens: [],
            type: ''
        }
    },
    computed: {
        ...mapGetters(['currentNetwork']),
    },
    async mounted() {
        const { outputCurrency, inputCurrency } = this.$route.query
        if (outputCurrency && outputCurrency.length == 42) {
            this.type = 'item2'
            await this.getCurrencyInfo(outputCurrency)
            this.importHandler()
        }
        if (inputCurrency && inputCurrency.length == 42) {
            this.type = 'item1'
            await this.getCurrencyInfo(inputCurrency)
            this.importHandler()
        }
        this.type = ''
    },
    watch: {
        'currentNetwork.ChainID': {
            immediate: true,
            handler() {
                let tokens = localStorage.getItem(this.currentNetwork.ChainID+'tokens')
                this.tokens = tokens ? JSON.parse(tokens) : this.currentNetwork.Token
                this.getTokens = toRaw(this.tokens)
                this.getBalance()
            }
        },
        searchTxt(val) {
            this.searchTxtChange(val)
        }
    },
    methods: {
        searchTxtChange(val) {
            if (!val) {
                this.getTokens = toRaw(this.tokens)
            }
            const list = this.tokens.filter(item => {
                const { name, address } = item
                if (name.toLocaleLowerCase().includes(val.toLocaleLowerCase()) || address.toLocaleLowerCase().includes(val.toLocaleLowerCase())) {
                    return true
                }
                return false
            })
            this.importTokens = {}
            this.getTokens = toRaw(list)
            if (list.length <= 0 && val.length === 42) {
                this.getCurrencyInfo(val)
            }
        },
        /**
         * 快捷导入
         * @param { String } addr 需要导入的地址
         * @param { Boolean } add 是否需要添加到代币列表
         */
        fastImport(addr, add = true) {
            addr = addr || this.searchTxt
            const item = this.tokens.find(item => {
                return item.address === addr
            })
            if (item) {
                this.selectHandler(item)
            } else {
                this.importTokens = {
                    name: addr.slice(0, 6),
                    symbol: addr.slice(0, 6),
                    address: addr,
                    isAdd: true
                }
                this.importHandler(add)
                const contract = window.wallet.initContract(addr)
                const promise = [contract.name(), contract.symbol()]
                Promise.all(promise).then(([name, symbol]) => {
                    this.updateTokenInfo(addr, name, symbol)
                })
            }
        },
        // 更新币种信息
        updateTokenInfo(address, name, symbol) {
            const tokensIndex = this.tokens.findIndex(item => {
                return item.address === address
            })
            if (tokensIndex >= 0) {
                this.tokens[tokensIndex].name = name
                this.tokens[tokensIndex].symbol = symbol
            }
            const index = this.getTokens.findIndex(item => {
                return item.address === address
            })
            if (index >= 0) {
                this.getTokens[index].name = name
                this.getTokens[index].symbol = symbol
            }
            this.$emit('update', {
                address, 
                name, 
                symbol
            })
            localStorage.setItem(this.currentNetwork.ChainID+'tokens', JSON.stringify(this.tokens))
        },
        // 删除币种
        removeTokens(index) {
            const item = this.getTokens[index]
            const tokenIndex = this.tokens.findIndex(child => {
                return item.address == child.address
            })
            this.tokens.splice(tokenIndex, 1)
            this.searchTxtChange(this.searchTxt)
            localStorage.setItem(this.currentNetwork.ChainID+'tokens', JSON.stringify(this.tokens))
        },
        async getCurrencyInfo(val) {
            const contract = window.wallet.initContract(val)
            const promise = [contract.name(), contract.symbol(), contract.decimals()]
            try {
                await Promise.all(promise).then(([name, symbol, decimals]) => {
                    this.importTokens = {
                        name,
                        symbol,
                        decimals,
                        address: val,
                        isAdd: true
                    }
                })
            } catch (error) {
                Tips(error)
            }
            
        },
        // 获取币种余额
        async getBalance() {
            if (!window.wallet.accounts[0]) {
                for (let i = 0; i < this.getTokens.length; i++) {
                    this.getTokens[i].balance = ''
                }
                return
            }
            let promise = []
            for (let i = 0; i < this.getTokens.length; i++) {
                const item = this.getTokens[i];
                let balance
                if (item.address === this.currentNetwork.MainCoin.address) {
                    balance = window.wallet.provider.getBalance()
                } else {
                    const contract = window.wallet.initContract(item.address)
                    balance = contract.balanceOf(window.wallet.accounts[0])
                }
                promise.push(balance)
            }
            Promise.allSettled(promise).then(data => {
                data.forEach((item, index) => {
                    const { status, value } = item
                    if (status === 'fulfilled') {
                        this.getTokens[index].balance = value
                    } else {
                        this.getTokens[index].balance = ''
                    }
                })
            })
        },

        /**
         * 导入新的币
         * @param { boolean } add 是否需要加入到代币列表
         */
        importHandler(add = true) {
            const item = this.tokens.find(item => {
                return item.address === this.importTokens.address
            })
            if (!item && add) {
                this.tokens.push(this.importTokens)
                this.searchTxt = ''
                localStorage.setItem(this.currentNetwork.ChainID+'tokens', JSON.stringify(this.tokens))
            }
            this.selectHandler(this.importTokens)
        },
        open(address) {
            this.activeAddress = address
            this.dialogVisible = true
            this.importTokens = {}
            this.getBalance()
        },
        selectHandler(data) {
            this.$emit('change', data, this.type)
            this.dialogVisible = false
            this.importTokens = {}
        }
    }
}
</script>

<style lang="scss">
.tokens-list__import-name{
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}
.tokens-list__import-desc{
    font-size: 14px;
    color: rgb(165, 165, 165);
}
.tokens-list__import-btn,.tokens-list__import-btn:hover{
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    color: #fff;
}
    .tokens{
        background: rgb(39, 38, 44);
        border-radius: 32px;
        max-height: 100vh;
        max-width: 100%;
        display: flex;
        flex-direction: column;

        .el-dialog__close{
            font-size: 20px;
            font-weight: bold;
        }
        .el-dialog__title{
            color: #FFF;
        }
        .el-dialog__header{
            border-bottom: 1px solid #5d5d5d;
        }
        .el-dialog__body{
            flex: 1;
            height: 100%;
            overflow: hidden;
            padding: 0;
        }
        .tokens-content{
            height: 100%;
        }
        .tokens-input{
            background-color: rgb(55, 47, 71);
            border-radius: 16px;
            color: rgb(244, 238, 255);
            display: block;
            font-size: 16px;
            height: 48px;
            outline: 0px;
            padding: 0px 16px;
            width: calc(100% - 60px);
            border: 1px solid rgb(38, 33, 48);
            margin: 20px;
        }
        .tokens-list{
            overflow: auto;
            max-height: 400px;
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                background: rgb(184, 173, 210);
                border-radius: 8px;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;
            }
            .tokens-list__item{
                padding: 20px 30px;
                transition: all .3s;
                &:hover{
                    background: rgb(8, 6, 11);
                }
                &.is-disabled{
                    background: #333;
                    opacity: .5;
                    cursor: not-allowed;
                }
            }
            .tokens-list__item-icon{
                width: 24px;
                height: 24px;
            }
            .token-list__item-title{
                color: #fff;
                font-size: 16px;
                font-weight: bold;
            }
            .token-list__item-desc{
                color: #999;
            }
            .token-list__item-price{
                font-size: 14px;
                color: #fff;
            }
        }
    }
</style>